<template>
    <!--  -->
    <div class="c_sup_detail hall_detail">
        <header-bar></header-bar>
        <!-- detail_banner -->
        <div class="big_banner">
            <img src="../../assets/images/big_banner.jpg">
        </div>
        <!--  -->
        <loop-scroll></loop-scroll>
        <!--  -->
        <div class="position_bar">
            <div class="g_wrap">
                <div class="pos_lt"> > </div>
                <div class="pos_rt">
                    <count-down :start="true"></count-down>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="lty_container">
            <back-top></back-top>
            <div class="g_wrap">
                <!--  -->
                <div class="lty_modal" v-show="isShowModal">
                    <div class="lty_modal_main">
                        <div class="lty_mdl_close" @click="closeModal"></div>
                        <!--  -->
                        <div class="lty_mdl_yj">
                            <div class="yj_id">ID{{res.listingNo}}</div>
                            <div class="yj_info">
                                <div class="info_tit"></div>
                                <ul class="info_lty01 clearfix">
                                    <li>{{res.listingTitle}}</li>
                                    <li>{{res.goodsName}}</li>
                                    <li>{{res.inventoryQtt}}</li>
                                    <li>{{res.goodsLevel}}</li>
                                    <li>{{res.listingPrice}}</li>
                                    <li>{{res.tradeUnit}}</li>
                                </ul>
                            </div>
                            <div class="yj_line"></div>
                            <div class="yj_info">
                                <div class="info_tit"></div>
                                <form class="yj_form">
                                    <div class="fm_item">
                                        <p>
                                            <label></label>
                                            <input type="text" v-model="quotePrice" name="quotePrice" v-validate:quotePrice.initial="'required|decimal:2|min_price'" data-vv-as="">
                                        </p>
                                        <p class="fm_err"><span v-show="errors.has('quotePrice')" class="help is-danger">{{ errors.first('quotePrice') }}</span></p>
                                    </div>
                                    <div class="fm_item">
                                        <p>
                                            <label></label>
                                            <input type="text" v-model="quoteQtt" name="quoteQtt" v-validate:quoteQtt.initial="'required|numeric'" data-vv-as="">
                                        </p>
                                        <p class="fm_err"><span v-show="errors.has('quoteQtt')" class="help is-danger">{{ errors.first('quoteQtt') }}</span></p>
                                    </div>
                                    <div class="fm_item">
                                        <p>
                                            <label></label>
                                            <input type="text" v-model="quotePremiumFee" name="quotePremiumFee" v-validate:quotePremiumFee.initial="'required|numeric'" data-vv-as="">
                                        </p>
                                        <p class="fm_err"><span v-show="errors.has('quotePremiumFee')" class="help is-danger">{{ errors.first('quotePremiumFee') }}</span></p>
                                    </div>
                                    <div class="fm_item">
                                        <p>
                                            <label></label>
                                            <input type="text" name="linkMan" v-model="linkMan" v-validate:linkMan.initial="'required'" data-vv-as="">
                                        </p>
                                        <p class="fm_err"><span v-show="errors.has('linkMan')" class="help is-danger">{{ errors.first('linkMan') }}</span></p>
                                    </div>
                                    <div class="fm_item">
                                        <p>
                                            <label></label>
                                            <input type="text" v-model="linkPhone" name="linkPhone" v-validate:linkPhone.initial="'required|tel'" data-vv-as="">
                                        </p>
                                        <p class="fm_err"><span v-show="errors.has('linkPhone')" class="help is-danger">{{ errors.first('linkPhone') }}</span></p>
                                    </div>
                                    <div class="fm_item">
                                        <p>
                                            <label></label>
                                            <input type="text" disabled :value="bizName">
                                        </p>
                                        <p class="fm_err"></p>
                                    </div>
                                    <div class="fm_item full">
                                        <p>
                                            <label></label>
                                            <textarea v-model="suggestion" name="suggestion" v-validate:suggestion.initial="'required|max:100'" data-vv-as=""></textarea>
                                        </p>
                                        <p class="fm_err"><span v-show="errors.has('suggestion')" class="help is-danger">{{ errors.first('suggestion') }}</span></p>
                                    </div>
                                    <div class="fm_btns">
                                        <span class="ok" @click="bidPriceSubmit"></span><span class="cancel"></span>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>

                <!--  -->
                <div class="lty_dtl_lt">
                    <!-- 1 -->
                    <div class="lty_01">
                        <options-bar></options-bar>
                    </div>
                    <!-- 2 -->
                    <div class="lty_02">
                        <div class="top">
                            <div class="top_lt"><img :src="res.pictureUrl"></div>
                            <div class="top_cr">
                                <div class="prd_name">
                                    <h3>{{res.listingTitle}}</h3>
                                </div>
                                <div class="prd_info">
                                    <ul class="clearfix">
                                        <li>{{res.goodsName}}</li>
                                        <li>{{res.dispatchPlace}}</li>
                                        <li>{{res.goodsLevel}}</li>
                                        <li>{{res.listingQtt}}</li>
                                        <li>{{res.inventoryQtt}}</li>
                                        <li>{{res.tradeUnit}}</li>
                                        <li>{{res.priceType}}</li>
                                        <li><i class="clr_red">{{res.listingPrice}}</i></li>
                                    </ul>
                                </div>
                                <div class="prd_btns">
                                    <a href="javascript:void(0);" class="btn_01" @click="showModal"></a>
                                    <a href="#" class="btn_02"></a>
                                    <a href="#" class="btn_03"></a>
                                </div>
                            </div>
                        </div>
                        <modal
                                title=""
                                v-model="isShowConfimModal"
                                width="260"
                                @on-ok="onConfimOk"
                                class-name="vertical-center-modal">
                            <p style="text-align: center;"></p>
                        </modal>
                        <modal
                                title=""
                                v-model="isShowLoginTips"
                                width="260"
                                @on-ok="onLogin"
                                class-name="vertical-center-modal">
                            <p style="text-align: center;"> ~</p>
                        </modal>
                        <!--  -->
                        <div class="c_detail_tabs">
                            <!-- tab_bar 1030,1186,1314,1504-->
                            <div class="tab_bar" :class="{ fix: isFixed }">
                                <span v-for="(val,key) in tabTitle" @click="goal(key,'#tab_item'+key)" :class="{active:key==isActive}">{{val}}</span>
                            </div>
                            <div class="tab_item" id="tab_item0">
                                <h3></h3>
                                <ul class="clearfix">
                                    <li>{{res.trabs}}</li>
                                    <li>{{res.fullOrBroken}}</li>
                                    <li>{{res.clarity}}</li>
                                    <li>{{res.colour}}</li>
                                    <li>{{res.smell}}</li>
                                    <li>{{res.taste}}</li>
                                    <li>{{res.liquorColor}}</li>
                                    <li>{{res.infusedLeaf}}</li>
                                </ul>
                            </div>
                            <div class="tab_item" id="tab_item1">
                                <h3></h3>
                                <ul class="clearfix">
                                    <li>{{res.goodsName}}</li>
                                    <li>{{res.goodsLevel}}</li>
                                    <li>{{res.wrapPattern}}</li>
                                    <li>{{res.origin}}</li>
                                </ul>
                            </div>
                            <div class="tab_item" id="tab_item2">
                                <h3></h3>
                                <ul class="clearfix">
                                    <li>{{res.listingQtt}}</li>
                                    <li>{{res.priceType}}</li>
                                    <li>{{res.tradeUnit}}</li>
                                </ul>
                            </div>
                            <div class="tab_item" id="tab_item3">
                                <h3></h3>
                                <ul class="clearfix">
                                    <li>{{res.csgBeginDate}}</li>
                                    <li>{{res.csgEndDate}}</li>
                                    <li>{{res.csgWarehouse}}</li>
                                    <li>{{res.qttCheckType}}</li>
                                    <li>{{res.qualityCheckType}}</li>
                                    <li>{{res.invoiceType}}</li>
                                    <li>{{res.storageLocation}}</li>
                                    <li>{{res.others}}</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <!--  -->
                <div class="lty_dtl_rt">
                    <!-- () -->
                    <div class="cpt_box seller_info">
                        <div class="cpt_box_title">
                            <i class="icon_sjxx"></i><span class="text"></span>
                        </div>
                        <div class="cpt_box_main">
                            <div class="c_seller_info">
                                <p class="new_line clearfix"><label></label><span>{{res.bizName}}</span></p>
                                <p class="new_line clearfix"><label></label><span>{{res.bizAddress}}</span></p>
                                <p>{{res.bizPhone}}</p>
                            </div>
                        </div>
                    </div>
                    <!-- () -->
                    <div class="cpt_box">
                        <div class="cpt_box_title">
                            <i class="icon_rxpz"></i><span class="text"></span>
                        </div>
                        <div class="cpt_box_main">
                            <product></product>
                            <product></product>
                            <product></product>
                        </div>
                    </div>
                    <!-- () -->
                    <div class="cpt_box">
                        <div class="cpt_box_title">
                            <i class="icon_tlxx"></i><span class="text"></span>
                        </div>
                        <div class="cpt_box_main">
                            <product></product>
                            <product></product>
                            <product></product>
                        </div>
                    </div>
                    <!-- () -->
                    <div class="cpt_box">
                        <div class="cpt_box_title">
                            <i class="icon_llxx"></i><span class="text"></span>
                        </div>
                        <div class="cpt_box_main">
                            <product></product>
                            <product></product>
                            <product></product>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <footer-bar></footer-bar>
        <success-modal msg="" :isShow="isShowSuccessBox"></success-modal>
    </div>
</template>
<script>
    /* supply/sup_tab_menu.vue */

    /* all-component */
    import HeaderBar from '../../components/header.vue'
    import FooterBar from '../../components/footer.vue'
    import LoopScroll from '../../components/loop_scroll.vue'
    import CountDown from '../../components/count_down.vue'
    import BackTop from '../../components/back_top.vue'
    import OptionsBar from './index/options.vue'
    import Product from './detail/product.vue'
    import SuccessModal from '../../components/success_modal'
    import axios from 'axios';

    import {mapGetters} from 'vuex'
    export default {
        data(){
            return {
                isShowConfimModal:false,
                isShowLoginTips: false,
                isShowSuccessBox:false,
                tabTitle:['','','',''],
                isFixed: false,
                isActive: 0,
                res:{},
                isShowModal: false,
                quotePrice:'',
                quoteQtt:'',
                quotePremiumFee:'',
                linkPhone:'',
                linkMan:'',
                suggestion:''
            }
        },
        computed: {
            ...mapGetters(['bizName', 'userId','userName'])
        },
        mounted(){
            this.linkMan = this.userName;
            let self = this;
            /*  */
            axios.post(this.$store.state.api,{
                "head": {
                    "sessionId":"000000",
                    "operatorType":"DoListingInfoQry"
                },
                "body": {
                    "listingId":this.$route.params.id
                }
            }).then(function (response) {
                let res = response.data.body[0];
                self.res = res;
                console.log(res);
            });

            this.fixedTabBar();
        },
        methods:{
            /*  */
            fixedTabBar(){
                let that = this;
                /*  */
                window.addEventListener("scroll", function () {
                    let pos = document.documentElement.scrollTop || document.body.scrollTop;
                    if (pos >= 1000) {
                        that.isFixed = true;
                        that.switchActive(pos);
                    } else {
                        that.isFixed = false;
                    }
                }, false);
            },
            switchActive(pos){
                if(pos<1189){
                    this.isActive = 0;
                }
                if(pos>1189 && pos<1317){
                    this.isActive = 1;
                }
                if(pos>1317 && pos<1507){
                    this.isActive = 2;
                }
                if(pos>1507){
                    this.isActive = 3;
                }
            },
            goal(i,g){
                let pos = this.$el.querySelector(g).offsetTop;
                pos = this.isFixed?pos-30:pos-60;
                this.isActive = i;
                document.body.scrollTop = pos;
                document.documentElement.scrollTop = pos;
            },
            showModal(){
                if(this.$store.state.sessionId != undefined && this.$store.state.sessionId != "000000"){
                    this.quotePrice = '';
                    this. quoteQtt = '';
                    this.quotePremiumFee = '';
                    this.linkPhone = '';
                    this.suggestion = '';
                    this.isShowModal = true;
                }else{
                    this.isShowLoginTips = true;
                }
            },
            closeModal(){
                this.isShowModal = false;
            },
            onConfimOk(){
                axios.post(this.$store.state.api,{
                    "head": {
                        "sessionId":"000000",
                        "operatorType":"DoQouteOrderInsert"
                    },
                    "body": {
                        "clientId":this.$store.state.clientId,
                        "listingId":this.$route.params.id,
                        "quotePrice":""+this.quotePrice+"",
                        "quoteQtt":""+this.quoteQtt+"",
                        "quotePremiumFee":""+this.quotePremiumFee+"",
                        "linkPhone":""+this.linkPhone+"",
                        "suggestion":""+this.suggestion+"",
                        "linkMan":""+this.linkMan+"",
                        "quoteBizName": ""+this.bizName+"",
                        "operatorId":""+this.userId+""
                    }
                }).then((response) =>{
                    console.log(response)
                    if(response.data.head.errCode == 0){
                        this.isShowSuccessBox = true;
                        let timer = setInterval(()=>{
                            this.isShowSuccessBox = false;
                            clearInterval(timer)
                            this.closeModal();
                            window.location.reload();
                        },1000);
                    }
                });
            },
            onLogin(){
                this.$router.push('/login')
            },
            bidPriceSubmit(){
                this.$validator.validateAll().then(() => {
                    console.log('From Submitted!');
                    this.isShowConfimModal = true;
                }).catch(() => {
                    console.log('Correct them errors!');
                });
            }
        },
        components: {
            HeaderBar,
            FooterBar,
            LoopScroll,
            CountDown,
            OptionsBar,
            BackTop,
            Product,
            SuccessModal
        }
    }
</script>
<style>
    @import "../../assets/css/home/ui_box.css";
    @import "../../assets/css/hall/detail.css";
    @import "../../assets/css/hall/detail_tab_bar.css";
    @import "../../assets/css/hall/lty_yjia_modal.css";
</style>


