<template>
    <!--  -->
    <div class="in_money_form">
        <form @submit.prevent="inMoneySubmit" class="oi_form">
            <div class="item">
                <label></label>
                <select name="bank" v-model="bank" v-validate:bank.initial="'min_value:1'" data-vv-as="">
                    <option value="0"></option>
                    <option value="1"></option>
                    <option value="2"></option>
                    <option value="3"></option>
                </select>
                <span v-show="errors.has('bank')" class="help is-danger"></span>
            </div>
            <div class="item">
                <label></label>
                <span></span>
            </div>
            <div class="item">
                <label></label>
                <span>6220**********898989</span>
            </div>
            <div class="item">
                <label></label>
                <input type="text" v-model="inmoney" name="inmoney" v-validate:inmoney.initial="'required|min:6'" data-vv-as="">
                <span v-show="errors.has('inmoney')" class="help is-danger">{{ errors.first('inmoney') }}</span>
            </div>
            <div class="item">
                <label></label>
                <input type="text" v-model="cardpwd" name="cardpwd" v-validate:cardpwd.initial="'required|min:6'" data-vv-as="">
                <span v-show="errors.has('cardpwd')" class="help is-danger">{{ errors.first('cardpwd') }}</span>
            </div>
            <div class="btn">
                <button type="submit"></button>
            </div>
        </form>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                bank:0,
                inmoney:'',
                cardpwd:''
            }
        },
        methods: {
            inMoneySubmit(){
                this.$validator.validateAll().then(() => {
                    console.log('From Submitted!');
                }).catch(() => {
                    console.log('Correct them errors!');
                });
            }
        }
    }
</script>
<style>
    @import "../../../assets/css/uc/uc_oi_money.css";
</style>


