<template>
    <!--  -->
    <div class="c_quotation">
        <header-bar></header-bar>
        <!--  -->
        <div class="position_bar">
            <div class="g_wrap">
                <div class="pos_lt"> > </div>
            </div>
        </div>
        <!--  -->
        <div class="lty_container">
            <div class="g_wrap">
                <!--  -->
                <div class="c_quo_lt">
                    <quot-sidebar :activeID="0"></quot-sidebar>
                </div>
                <!--  -->
                <div class="c_quo_rt">
                    <div class="quo_title"></div>
                    <!--  -->
                    <div class="quo_search clearfix">
                        <div class="sch_item">
                            <label></label>
                            <select name="type">
                                <option value="0"></option>
                                <option value="1"></option>
                                <option value="2"></option>
                                <option value="3"></option>
                                <option value="4"></option>
                            </select>
                        </div>
                        <div class="sch_item daterange">
                            <label></label>
                            <calendar size="small" type="date" format="yyyy/MM/dd" placeholder=""></calendar>
                        </div>
                        <div class="sch_btn"></div>
                    </div>
                    <!--  -->
                    <table class="quo_list" style="table-layout: fixed;">
                        <thead>
                            <tr>
                                <th width="8%"></th>
                                <th width="9%"></th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="10%"></th>
                                <th width="9%"></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in list">
                                <td>{{item.a}}</td>
                                <td>{{item.b}}</td>
                                <td :class="[item.h.substr(0,1)=='-' ? 'gn' : 'rd']">{{item.c}}</td>
                                <td :class="[item.h.substr(0,1)=='-' ? 'gn' : 'rd']">{{item.d}}</td>
                                <td :class="[item.h.substr(0,1)=='-' ? 'gn' : 'rd']">{{item.e}}</td>
                                <td :class="[item.h.substr(0,1)=='-' ? 'gn' : 'rd']">{{item.f}}</td>
                                <td :class="[item.h.substr(0,1)=='-' ? 'gn' : 'rd']">{{item.g}}</td>
                                <td :class="[item.h.substr(0,1)=='-' ? 'gn' : 'rd']">{{item.h}}</td>
                                <td>{{item.i}}</td>
                                <td>{{item.j}}</td>
                                <td>{{item.n}}</td>
                                <td>{{item.l}}</td>
