        <footer-bar></footer-bar>
    </div>
</template>
<script>
    /* all-component */
    import HeaderBar from '../../components/header.vue'
    import FooterBar from '../../components/footer.vue'
    import QuotSidebar from './index/quot_sidebar.vue'

    export default {
        data(){
            return {
                list:[
                    {
                        a:'',
                        b:'201706',
                        c:100.00,
                        d:112,
                        e:11,
                        f:43,
                        g:23,
                        h:'10%',
                        i:23,
                        j:29,
                        n:123,
                        l:233
                    },
                    {
                        a:'',
                        b:'201706',
                        c:100.00,
                        d:112,
                        e:11,
                        f:43,
                        g:23,
                        h:'-23%',
                        i:23,
                        j:29,
                        n:123,
                        l:233
                    }
                ]
            }
        },
        components: {
            HeaderBar,
            FooterBar,
            QuotSidebar
        }
    }
</script>
<style>
    @import "../../assets/css/quotation/lty_quotation.css";
</style>


