<template>
    <!--  -->
    <div class="out_money_form">
        <form @submit.prevent="outMoneySubmit" class="oi_form">
            <div class="item">
                <label></label>
                <span class="info"> <i>8923423.00</i></span>
                <span class="info"> <i>8923423.00</i></span>
            </div>
            <div class="item">
                <label></label>
                <select name="bank" v-model="bank" v-validate:bank.initial="'min_value:1'" data-vv-as="">
                    <option value="0"></option>
                    <option value="1"></option>
                    <option value="2"></option>
                    <option value="3"></option>
                </select>
                <span v-show="errors.has('bank')" class="help is-danger"></span>
            </div>
            <div class="item">
                <label></label>
                <span></span>
            </div>
            <div class="item">
                <label></label>
                <span>6220**********898989</span>
            </div>
            <div class="item">
                <label></label>
                <input type="text" v-model="outmoney" name="outmoney" v-validate:outmoney.initial="'required|min:6'" data-vv-as="">
                <span v-show="errors.has('outmoney')" class="help is-danger">{{ errors.first('outmoney') }}</span>
            </div>
            <div class="item">
                <label></label>
                <input type="text" v-model="paypwd" name="paypwd" v-validate:paypwd.initial="'required|min:6'" data-vv-as="">
                <span v-show="errors.has('paypwd')" class="help is-danger">{{ errors.first('paypwd') }}</span>
            </div>
            <div class="btn">
                <button type="submit"></button>
            </div>
        </form>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                bank:0,
                outmoney:'',
                paypwd:''
            }
        },
        methods: {
            outMoneySubmit(){
                this.$validator.validateAll().then(() => {
                    console.log('From Submitted!');
                }).catch(() => {
                    console.log('Correct them errors!');
                });
            }
        }
    }
</script>
<style>
    @import "../../../assets/css/uc/uc_oi_money.css";
    .oi_form .info{
        margin-right: 30px;
    }
    .oi_form .info i{
        color: #ff0000;
    }
</style>


