<template>
    <!--  -->
    <div class="c_modify_pwd">
        <header-bar></header-bar>
        <!--  -->
        <div class="position_bar">
            <div class="g_wrap">
                <div class="pos_lt"> > </div>
            </div>
        </div>
        <!--  -->
        <div class="lty_container">
            <div class="g_wrap">
                <!--  -->
                <uc-sidebar></uc-sidebar>

                <!-- content -->
                <div class="uc_main">
                    <!-- uc_main_title -->
                    <div class="ucm_title"> </div>
                    <form class="common_form" @submit.prevent="modifyPwd">
                        <div class="fm_item">
                            <label></label>
                            <input type="password" v-model="oldpwd" name="oldpwd" v-validate:oldpwd.initial="'required'" data-vv-as="" placeholder="">
                        </div>
                        <div class="fm_err"><span v-show="errors.has('oldpwd')" class="help is-danger">{{ errors.first('oldpwd') }}</span></div>
                        <div class="fm_item">
                            <label></label>
                            <input type="password" v-model="newpwd" name="newpwd" v-validate:newpwd.initial="'required|min:6'" data-vv-as="" placeholder="">

                        </div>
                        <div class="fm_err"><span v-show="errors.has('newpwd')" class="help is-danger">{{ errors.first('newpwd') }}</span></div>
                        <div class="fm_item">
                            <label></label>
                            <input type="password" v-model="renewpwd" name="renewpwd" v-validate:renewpwd.initial="'required|confirmed:newpwd'" data-vv-as="" placeholder="">
                        </div>
                        <div class="fm_err"><span v-show="errors.has('renewpwd')" class="help is-danger">{{ errors.first('renewpwd') }}</span></div>
                        <div class="fm_btn">
                            <button class="g_btn"></button>
                        </div>
                    </form>

                </div>
            </div>
        </div>
        <footer-bar></footer-bar>
        <success-modal msg=", " :isShow="isShowModal"></success-modal>
    </div>
</template>
<script>
    /* all-component */
    import HeaderBar from '../../components/header.vue'
    import FooterBar from '../../components/footer.vue'
    import UcSidebar from './index/uc_sidebar.vue'
    import SuccessModal from '../../components/success_modal'

    import axios from 'axios'

    export default{
        data () {
            return {
                isShowModal:false,
                oldpwd:'',
                newpwd:'',
                renewpwd:''
            }
        },
        methods: {
            modifyPwd(){
                this.$validator.validateAll().then(() => {
                    console.log('From Submitted!');
                    axios.post(this.$store.state.api,{
                        "head": {
                            "sessionId":this.$store.state.sessionId,
                            "operatorType":"DoPasswordUpdate"
                        },
                        "body":{
                            "clientId": this.$store.state.clientId,
                            "oldPassword": this.oldpwd,
                            "newPassword": this.newpwd,
                            "newPasswordConfirm": this.renewpwd
                        }
                    }).then((response)=> {
                        console.log(response);
                        if(response.data.head.errCode == 0){
                            this.isShowModal = true;
                            let timer = setInterval(()=>{
                                this.isShowModal = false;
                                this.$store.dispatch('signOut');
                                this.$router.push('/login');
                                clearInterval(timer)
                            },1000);
                        }

                    })
                }).catch(() => {
                    console.log('Correct them errors!');
                });
            }
        },
        components:{
            HeaderBar,
            FooterBar,
            UcSidebar,
            SuccessModal
        }
    }
</script>
<style>
    @import "../../assets/css/uc/uc_form.css";
    .c_modify_pwd .g_btn{
        margin-left: 90px;
    }
</style>


