<template>
    <div class="c_hall c_quote">
        <header-bar></header-bar>
        <!-- detail_banner -->
        <div class="big_banner">
            <img src="../../assets/images/big_banner.jpg">
        </div>
        <!--  -->
        <loop-scroll></loop-scroll>
        <!--  -->
        <div class="position_bar">
            <div class="g_wrap">
                <div class="pos_lt"> > </div>
                <div class="pos_rt">
                    <count-down :start="true"></count-down>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="lty_container">
            <div class="g_wrap clearfix">
                <!--  -->
                <div class="lty_ctr_lt">
                    <!-- 1 -->
                    <div class="lty_01">
                        <options-bar></options-bar>
                    </div>

                    <!-- 2 -->
                    <div class="lty_02">
                        <!-- top -->
                        <div class="lty_02_top">
                            <h3></h3>
                            <p><span>2016-02-21 10:22:19</span><span>278</span></p>
                        </div>
                        <!-- info -->
                        <div class="lty_02_ctr">
                            <div class="info_title"></div>
                            <div class="info_detail clearfix">
                                <!--  -->
                                <div class="info_dtl_lt">
                                    <p></p>
                                    <p>010-00999399</p>
                                </div>
                                <!--  -->
                                <div class="info_dtl_cr">
                                    <p></p>
                                    <p>12312312312@qq.com</p>
                                </div>
                                <!--  -->
                                <div class="info_dtl_rt">
                                    <p><span></span><span></span></p>
                                    <p>
                                        
                                        <input type="text" v-model="price" name="price" v-validate:price.initial="'required|numeric'" data-vv-as="">
                                        <span v-show="errors.has('price')" class="help is-danger">{{ errors.first('price') }}</span>
                                    </p>
                                    <p>
                                        
                                        <input type="text" v-model="number" name="number" v-validate:number.initial="'required|numeric'" data-vv-as="">
                                        <span v-show="errors.has('number')" class="help is-danger">{{ errors.first('number') }}</span>
                                    </p>
                                </div>
                            </div>
                        </div>
                        <!-- btns -->
                        <div class="lty_02_bom">
                            <span class="g_btn" @click="okSupply"></span>
                        </div>
                    </div>
                </div>
                <!--  -->
                <div class="lty_ctr_rt">
                    <!-- () -->
                    <div class="cpt_box lty_jyrl">
                        <div class="cpt_box_title">
                            <i class="icon_jyrl"></i><span class="text"></span>
                        </div>
                        <div class="cpt_box_main">
                            <calendar type="date" :open="true" placeholder="" style="width:100px"></calendar>
                        </div>
                    </div>
                    <!-- () -->
                    <div class="cpt_box">
                        <div class="cpt_box_title">
                            <i class="icon_pzhq"></i><span class="text"></span>
                        </div>
                        <div class="cpt_box_main">
                            <p>123</p>
                            <p>123</p>
                            <p>123</p>
                            <p>123</p>
                            <p>123</p>
                        </div>
                    </div>
                    <!-- () -->
                    <div class="cpt_box lty_jptg">
                        <div class="cpt_box_title">
                            <i class="icon_qgxx"></i><span class="text"></span><a href="#" class="more"></a>
                        </div>
                        <div class="cpt_box_main">
                            <div class="cpt_list">
                                <ul>
                                    <li>
                                        <a href="#" class="g_ellipsis">1112016(120g)-</a>
                                    </li>
                                    <li>
                                        <a href="#" class="g_ellipsis">2222016(120g)-</a>
                                    </li>
                                    <li>
                                        <a href="#" class="g_ellipsis">3332016(120g)-</a>
                                    </li>
                                    <li>
                                        <a href="#" class="g_ellipsis">4442016(120g)-</a>
                                    </li>
                                    <li>
                                        <a href="#" class="g_ellipsis">5552016(120g)-</a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <footer-bar></footer-bar>
    </div>
</template>
<script>
    /* all-component */
    import HeaderBar from '../../components/header.vue'
    import FooterBar from '../../components/footer.vue'
    import LoopScroll from '../../components/loop_scroll.vue'
    import CountDown from '../../components/count_down.vue'
    import OptionsBar from './index/options.vue'

    export default {
        data(){
            return {
                price:'',
                number:''
            }
        },
        mounted(){},
        methods: {
            okSupply(){
                this.$validator.validateAll().then(() => {
                    console.log('From Submitted!');
                    console.log(this.price);
                    console.log(this.number);
                }).catch(() => {
                    console.log('Correct them errors!');
                });
            }
        },
        components: {
            HeaderBar,
            FooterBar,
            LoopScroll,
            CountDown,
            OptionsBar
        }
    }
</script>
<style>
    @import '../../assets/css/home/ui_list.css';
    @import '../../assets/css/common/position_bar.css';
    @import '../../assets/css/hall/hall.css';

    .c_quote .lty_02{
        padding: 20px 25px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
    .c_quote .lty_02 .lty_02_top{
        padding-bottom: 20px;
        border-bottom: 1px solid #e0e0e0;
    }
    .c_quote .lty_02_top h3{
        font-size: 24px;
        margin-bottom: 5px;
    }
    .c_quote .lty_02_top p{
        color: #888;
        font-size: 12px;
    }
    .c_quote .lty_02_top p span{
        margin-right: 60px;
    }
    .c_quote .lty_02 .lty_02_ctr{
        padding: 30px 0;
        border-bottom: 1px solid #e0e0e0;
    }
    .c_quote .info_title {
        font-size: 18px;
        margin-bottom: 30px;
    }
    .c_quote .info_detail p{
        color: #555;
        margin-bottom: 15px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .c_quote .info_detail p span{
        margin-right: 30px;
    }
    .c_quote .info_detail input{
        width: 100px;
        height: 20px;
        line-height: 20px;
        border: 1px solid #888;
        border-radius: 2px;
    }
    .c_quote .info_dtl_lt{
        width: 180px;
        float: left;
        margin-right: 10px;
    }
    .c_quote .info_dtl_cr{
        width: 280px;
        float: left;
    }
    .c_quote .info_dtl_rt{
        width: 400px;
        float: left;
    }
    .c_quote .lty_02_bom{
        text-align: center;
        padding: 30px 0 300px;
    }
    .c_quote .lty_02_bom .g_btn{
        color: #fff;
        font-size: 16px;
        padding: 5px 20px;
        cursor: pointer;
        background-color: #f02d2d;
    }
    .c_quote .help.is-danger{
        position: relative;
        top: -1px;
        color: #f02d2d;
        font-size: 12px;
    }
</style>


