<script>
    /* all-component */
    import HeaderBar from '../../components/header.vue'
    import FooterBar from '../../components/footer.vue'
    import LoopScroll from '../../components/loop_scroll.vue'
    import CountDown from '../../components/count_down.vue'
    import BackTop from '../../components/back_top.vue'
    import OptionsBar from './index/options.vue'
    import Product from './detail/product.vue'

    export default {
        data(){
            return {
                isShowModal: false,
                tabTitle:['','',''],
                isFixed: false,
                isActive: 0
            }
        },
        mounted(){
            this.fixedTabBar();
        },
        methods: {
            showModal(){
                this.isShowModal = true;
            },
            closeModal(){
                this.isShowModal = false;
            },
            /*  */
            fixedTabBar(){
                let that = this;
                /*  */
                window.addEventListener("scroll", function () {
                    let pos = document.documentElement.scrollTop || document.body.scrollTop;
                    if (pos >= 1000) {
                        that.isFixed = true;
                        if(document.body.clientHeight>2000){
                            that.switchActive(pos);
                        }
                    } else {
                        that.isFixed = false;
                    }
                }, false);
            },
            switchActive(pos){
                if(pos<1189){
                    this.isActive = 0;
                }
                if(pos>1189 && pos<1317){
                    this.isActive = 1;
                }
                if(pos>1317 && pos<1507){
                    this.isActive = 2;
                }
                if(pos>1507){
                    this.isActive = 3;
                }
            },
            goal(i,g){
                console.log(g);
                let pos = this.$el.querySelector(g).offsetTop;
                pos = this.isFixed?pos-30:pos-60;
                this.isActive = i;
                document.body.scrollTop = pos;
                document.documentElement.scrollTop = pos;
            }
        },
        components: {
            HeaderBar,
            FooterBar,
            LoopScroll,
            CountDown,
            OptionsBar,
            BackTop,
            Product
        }
    }
</script>
<style>
