<template>
    <!--  -->
    <div class="c_order_detail">
        <header-bar></header-bar>
        <!--  -->
        <div class="position_bar">
            <div class="g_wrap">
                <div class="pos_lt"> > </div>
            </div>
        </div>
        <!--  -->
        <div class="lty_container">
            <div class="g_wrap">
                <!--  -->
                <uc-sidebar></uc-sidebar>
                <!-- content -->
                <div class="uc_main">
                    <!-- uc_main_title -->
                    <div class="ucm_title"></div>
                    <dl class="dtl_item">
                        <dt>{{orderInfo.deal_status}}</dt>
                        <dd class="clearfix">
                            <span>{{orderInfo.deal_id}}</span>
                            <span>id{{orderInfo.listing_id}}</span>
                            <span>{{orderInfo.deal_price}}/</span>
                            <span>{{orderInfo.listing_title}}</span>
                            <span>{{orderInfo.deal_premium_fee}}/</span>
                            <span>{{orderInfo.listing_qtt}}</span>
                            <span>{{orderInfo.deal_qtt}}</span>
                            <span>{{orderInfo.over_flow_dif}}</span>
                            <span>{{orderInfo.deposit_type}}</span>
                            <span>,</span>
                            <span>,</span>
                            <span>{{orderInfo.deal_date}}</span>
                        </dd>
                    </dl>
                    <dl class="dtl_item">
                        <dt></dt>
                        <dd class="clearfix">
                            <span>1</span>
                            <span>XXX</span>
                            <span>100</span>
                            <span>2017-04-30</span>
                            <span class="full"></span>
                        </dd>
                    </dl>
                </div>
            </div>
        </div>
        <footer-bar></footer-bar>
    </div>
</template>
<script>
    /* all-component */
    import HeaderBar from '../../components/header.vue'
    import FooterBar from '../../components/footer.vue'
    import UcSidebar from './index/uc_sidebar.vue'

    import axios from 'axios'
    export default{
        data(){
            return {
                orderInfo:''
            }
        },
        mounted(){
            this.getInfoById();
        },
        methods:{
            getInfoById(){
                /*  */
                axios.post(this.$store.state.api, {
                    "head": {
                        "sessionId": this.$store.state.sessionId,
                        "operatorType": "DoDealOrderQry"
                    },
                    "body": {
                        "clientId":this.$store.state.clientId,
                        "dealId":this.$route.params.id
                    }
                }).then((response) => {
                    this.orderInfo = response.data.body[0].data[0];
                });
            }
        },
        components:{
            HeaderBar,
            FooterBar,
            UcSidebar
        }
    }
</script>
<style>
    @import "../../assets/css/uc/uc_detail_lty.css";
</style>


