<template>
    <!--  -->
    <div class="c_notice">
        <header-bar></header-bar>
        <!--  -->
        <div class="position_bar">
            <div class="g_wrap">
                <div class="pos_lt"> > </div>
            </div>
        </div>
        <!--  -->
        <div class="lty_container">
            <div class="g_wrap">
                <!--  -->
                <div class="c_notice_lt">
                    <quot-sidebar :activeID="activeID"></quot-sidebar>
                </div>
                <!--  -->
                <div class="c_notice_rt">
                    <h3 class="notice_main_title"></h3>
                    <p class="notice_main_date">2017/02/04</p>
                    <div class="notice_main_cont">
                        <p></p>
                    <p>1224</p>
                    <p>2016105</p>
                    <p>12</p>
                    <p></p>
                    <p>1234www.dce.com.cn--</p>
                        <p></p>
                    </div>
                </div>
            </div>
        </div>
        <footer-bar></footer-bar>
    </div>
</template>
<script>
    /* all-component */
    import HeaderBar from '../../components/header.vue'
    import FooterBar from '../../components/footer.vue'
    import QuotSidebar from './index/notice_sidebar.vue'

    let active_id = 0;
    export default {
        data(){
            return {
                activeID:active_id
            }
        },
        beforeRouteEnter(to, from, next){
            let from_router = from.fullPath.substr(1);
            switch (from_router){
                case 'notice':
                    active_id = 0;
                    break;
                case 'business_notice':
                    active_id = 1;
                    break;
                case 'active_notice':
                    active_id = 2;
                    break;
                case 'info_notice':
                    active_id = 3;
                    break;
                case 'exchange_dynamic':
                    active_id = 4;
                    break;
            }
            next();
        },
        components: {
            HeaderBar,
            FooterBar,
            QuotSidebar
        }
    }
</script>
<style>
    @import "../../assets/css/notice/notice.css";
</style>


