<template>
    <!--  -->
    <div class="c_uc_info">
        <header-bar></header-bar>
        <!--  -->
        <div class="position_bar">
            <div class="g_wrap">
                <div class="pos_lt"> > </div>
            </div>
        </div>
        <!--  -->
        <div class="lty_container">
            <div class="g_wrap">
                <!--  -->
                <uc-sidebar></uc-sidebar>

                <!-- content -->
                <div class="uc_main">
                    <!-- uc_main_title -->
                    <div class="ucm_title"> </div>

                    <!--  -->
                    <div class="uc_info_cont clearfix" v-show="userStatus != 3">
                        <div class="info_cont_lt">
                            <img src="../../assets/images/sm_gg1.jpg">
                        </div>
                        <div class="info_cont_rt">
                            <!--  -->
                            <div class="common_form">
                                <div class="fm_item"><label>ID</label><span>{{user.client_id}}</span></div>
                                <div class="fm_item"><label></label><span>{{user.mobile_phone}}</span></div>
                                <div class="fm_btn" v-if="userStatus == 1"><a href="#/identity_auth" class="g_btn"></a></div>
                                <div class="fm_btn" v-else-if="userStatus == 2"><span class="g_btn">, </span></div>
                                <div class="fm_btn" v-else-if="userStatus == 3"><span class="g_btn">,</span></div>
                                <div class="fm_btn" v-else="userStatus == 4"><span class="g_btn">,</span></div>
                            </div>
                        </div>
                    </div>
                    <div class="info_cont_tip">
                        
                        
                    </div>


                    <!--  -->
                    <div class="uc_info_cont clearfix" v-show="userStatus == 3">
                        <div class="info_cont_lt">
                            <img src="../../assets/images/sm_gg1.jpg">
                        </div>
                        <div class="info_cont_rt">
                            <div class="common_form" v-show="true">
                                <div class="fm_item"><label></label><span>{{user.mobile_phone}}</span><a href="#/modify_info" class="modify"></a></div>
                                <div class="fm_item"><label></label><span>{{user.link_man}}</span></div>
                                <div class="fm_item">
                                    <label></label>
                                    <div class="gender">
                                        <label><input type="radio" name="sex" v-model="sex" value="1"></label>
                                        <label><input type="radio" name="sex" v-model="sex" value="2"></label>
                                    </div>
                                </div>
                                <div class="fm_item"><label></label><span>{{user.company_name}}</span></div>
                                <div class="fm_item"><label></label><span>{{user.company_type==1?'':''}}</span></div>
                                <div class="fm_item"><label></label><span>{{user.company_address}}</span></div>
                                <div class="fm_item"><label></label><span>{{user.company_link_man}}</span></div>
                                <div class="fm_item"><label></label><span>{{user.company_phone}}</span></div>
                                <div class="fm_item"><label></label><span>{{user.company_email}}</span></div>
                                <div class="fm_item">
                                    <label></label>
                                    <img :src="user.business_license_photo" style="width: 91px;height: 91px;">
                                </div>
                                <div class="fm_item">
                                    <label></label>
                                    <img :src="user.organization_code_photo" style="width: 91px;height: 91px;">
                                </div>
                                <div class="fm_item">
                                    <label></label>
                                    <img :src="user.tax_photo" style="width: 91px;height: 91px;">
                                </div>
                                <div class="fm_btn">
                                    <a href="#/create_product" class="g_btn blue"></a>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
        <footer-bar></footer-bar>
    </div>
</template>
<script>
    /* all-component */
    import HeaderBar from '../../components/header.vue'
    import FooterBar from '../../components/footer.vue'
    import UcSidebar from './index/uc_sidebar.vue'
    import axios from 'axios'

    export default{
        data(){
            return {
                user:'',
                userStatus:'',
                sex:''
            }
        },
        mounted(){
            this.userStatus = this.$store.state.userStatus;
            this.getUserInfo();
        },
        methods:{
            getUserInfo(){
                axios.post(this.$store.state.api,{
                    "head": {
                        "sessionId":this.$store.state.sessionId,
                        "operatorType":"DoClientInfoQry"
                    },
                    "body": {
                        "clientId":this.$store.state.clientId
                    }
                }).then((response)=> {
                    let res = response.data;
                    if(res.head.errCode == 0){
                        this.user = res.body[0];
                        this.sex = res.body[0].sex;
                        console.log(res.body[0])
                    }
                })
            }
        },
        components:{
            HeaderBar,
            FooterBar,
            UcSidebar
        }
    }
</script>
<style>
    @import "../../assets/css/uc/uc_form.css";
    .c_uc_info .common_form{
        padding-left: 0;
    }
    .c_uc_info .info_cont_lt{
        width: 120px;
        height: 120px;
        float: left;
    }
    .c_uc_info .info_cont_lt img{
        width: 100%;
        height: 100%;
    }
    .c_uc_info .info_cont_rt{
        width: 600px;
        float: right;
    }
    .c_uc_info .fm_item {
        margin-bottom: 15px;
    }
    .c_uc_info .info_cont_tip{
        color: #f02d2d;
        margin: 30px 0;
    }
    .c_uc_info .modify {
        float: right;
        color: #65d5ff;
    }
    .c_uc_info .blue {
        margin-right: 5px;
        background-color: #3aa9f2 !important;
    }
</style>


