<template>
    <div class="c_supply_list pur_sup_auc">
        <!--  -->
        <header-bar></header-bar>
        <div class="lty_container">
            <!--  -->
            <div class="position_bar">
                <div class="g_wrap">
                    <div class="pos_lt"> > </div>
                </div>
            </div>
            <div class="g_wrap list_main">
                <h3 class="list_title"></h3>
                <!--  -->
                <div class="c_list_search">
                    <div class="sch_top">
                        <div class="items">
                            <div class="items_lt"></div>
                            <div class="items_rt">
                                <div class="options">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                            </div>
                        </div>
                        <div class="items">
                            <div class="items_lt"></div>
                            <div class="items_rt">
                                <div class="options">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                            </div>
                        </div>
                        <div class="items">
                            <div class="items_lt"></div>
                            <div class="items_rt">
                                <div class="options">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                            </div>
                        </div>
                        <div class="items" :class="{toggle:isToggle1}">
                            <div class="items_lt"></div>
                            <div class="items_rt">
                                <div class="options">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                                <div class="btn_toggle" @click="toggleDown1">{{btnText1}}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="list_box clearfix">
                    <div class="sort_bar clearfix">
                        <div class="btn_sort active"> <i class="icon_arrow"></i></div>
                        <div class="btn_sort"> <i class="icon_arrow"></i></div>
                        <div class="btn_sort btn_price"> <i class="icon_arrow"></i></div>
                    </div>
                    <div class="c_sup_tab pur_sup">
                        <table style="table-layout:fixed">
                            <thead>
                            <tr>
                                <th width="10%"></th>
                                <th width="10%"></th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="6%"></th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="8%">(/)</th>
                                <th width="8%">()</th>
                                <th width="8%"></th>
                                <th width="8%"></th>
                                <th width="10%"></th>
                            </tr>
                            </thead>
                            <tbody>
                                <tr v-for="item in list">
                                    <td>{{item.listingTitle}}</td>
                                    <td>{{item.bizName}}</td>
                                    <td>{{item.csgPlace}}</td>
                                    <td>{{item.buyorsell}}</td>
                                    <td>{{item.goodsName}}</td>
                                    <td>{{item.goodsLevel}}</td>
                                    <td>{{item.priceType}}</td>
                                    <td>{{item.listingPrice}}</td>
                                    <td>{{item.listingQtt}}</td>
                                    <td>{{item.listingDate}}</td>
                                    <td>{{item.listingStatus}}</td>
                                    <td>
                                        <a :href="'#/supply/'+item.listingId" class="go_detail"></a>
                                        <a href="#" class="add_cart"></a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <page :total="total" :page-size="pageSize" @on-change="switchPage" size="small" class-name="c_page"></page>
            </div>
        </div>
        <!--  -->
        <footer-bar></footer-bar>
    </div>
</template>
<script>

    import HeaderBar from '../../components/header.vue'
    import FooterBar from '../../components/footer.vue'
    import axios from 'axios';

    export default {
        data(){
            return {
                list:[],
                btnText1: '',
                isToggle1:false,
                total:0,
                pageSize:6
            }
        },
        mounted(){
            let self = this;
            /*  */
            axios.post(this.$store.state.api,{
                "head": {
                    "sessionId":"000000",
                    "operatorType":"DoListingCommnQry"
                },
                "body": {}
            }).then(function (response) {
                let res = response.data.body[0];
                console.log(res);
                self.list = res.data;
                self.total = res.totalCounts;
            });
        },
        methods: {
            toggleDown1(){
                this.isToggle1 = !this.isToggle1;
                this.isToggle1?this.btnText1 = '':this.btnText1 = '';
            },
            switchPage(pageIndex){
              this.getListByPageIndex(pageIndex);
            },
            getListByPageIndex(pageIndex){
                axios.post(this.$store.state.api,{
                    "head": {
                        "sessionId":"000000",
                        "operatorType":"DoListingCommnQry"
                    },
                    "body": {
                        "pageSize":this.pageSize,
                        "pageCount":pageIndex
                    }
                }).then((response)=>{
                    let res = response.data.body[0];
                    this.list = res.data;
                });
            }
        },
        components: {
            HeaderBar,
            FooterBar,
        }
    }
</script>
<style>
    @import "../../assets/css/hall/sort_bar.css";
    @import "../../assets/css/hall/pur_sup_list.css";
    @import "../../assets/css/hall/search_bar.css";
    @import "../../assets/css/hall/pur_sup_table.css";
    @import "../../assets/css/common/page.css";

</style>


